.container {
  background-color: #FE0041;
}
.spinner {
  border-width: 8px;
  border-style: solid;
  border-color: #7D5195 #7D5195 #7D5195 transparent;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  animation: spin 1.5s infinite;
  text-align: center;
  margin: 0.5rem 0 0 150px;
  
}
.spinner:before, .spinner:after {
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #7D5195;
  position: absolute;
  /* left: 8px; */
}
.spinner:before {
  top: 8px;
}
.spinner:after {
  bottom: 8px;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}